import './style'
import { Component } from 'preact'
// import ScaleText from "react-scale-text"
import firebase from './firebase.js'
import './assets/style.css'

const Logo = require('./assets/flexicon.svg')

export default class App extends Component {
	state = {
		word: '',
		translation: '',
		translations: [],
		addWord: '',
		addTranslation: '',

		showStartPage: true,
		showAddNewWord: false,
		showWordAdded: false,
		plusHover: false,
		showShare: false
	}

	isShowAddNewBlock = false;

	greet = ''

	handleChange = (e) => {
		this.setState({ word: e.target.value })
		this.checkWord()
	}

	handleAddWordChange = (e) => {
		this.setState({ addWord: e.target.value })
	}

	handleAddTranslationChange = (e) => {
		this.setState({ addTranslation: e.target.value })
	}

	checkWord = () => {
		this.setState({ translation: '' })
		let obj = this.state.translations.find(word => word.word === this.state.word)
		if (obj) {
			this.setState({ translation: obj.translation })
		}
	}

	addTranslation = () => {
		const translationsRef = firebase.database().ref('translations');
		const item = {
			word: this.state.addWord,
			translation: this.state.addTranslation
		}
		if (this.state.addWord.length > 0 && this.state.addTranslation.length > 0) {
			translationsRef.push(item);
			this.setState({
				addWord: '',
				addTranslation: '',
				showWordAdded: true
			});
		}
	}

	populate = () => {
		const translationsRef = firebase.database().ref('translations');

		translations.map(o => translationsRef.push(o));
	}

	showAddNew = () => {
		this.setState({ plusHover: true })
	}

	hideAddNew = () => {
		this.setState({ plusHover: false })
	}

	showShare = () => {
		this.setState({ showShare: true })
	}

	hideShare = () => {
		this.setState({ showShare: false })
	}

	toggleAddNewWord = () => {
		this.setState({
			showAddNewWord: !this.state.showAddNewWord,
			showWordAdded: false,
			word: '',
			translation: ''
		})
		this.isShowAddNewBlock ? this.isShowAddNewBlock = false : setTimeout(() => this.isShowAddNewBlock = true, 100)
	}

	hideStartPage = () => {
		this.setState({ showStartPage: false })
	}

	fbShare = () => {
		FB.ui({
			method: 'share',
			href: `https://www.linkedin.com/shareArticle?mini=true&url=${window.location.href}?word=${this.state.word}`
		}, function(response){});
	}

	componentDidMount() {
		this.greet = this.getRandomString()
		const translationsRef = firebase.database().ref('translations');
		translationsRef.on('value', (snapshot) => {
			let translations = snapshot.val()
			let newState = []
			for (let item in translations) {
				newState.push({
					id: item,
					word: translations[item].word,
					translation: translations[item].translation
				})
			}
			this.setState({
				translations: newState
			})

			const url = new URL(window.location.href);
			const searchParam = url.searchParams.get("word");
			if (searchParam) {
				this.setState({ addWord: searchParam })
				this.setState({ word: searchParam })
				this.checkWord()
				this.setState({ showStartPage: false }) 
			}
		})
		
		document.onclick = (e) => {
			if(e.target.className !== 'add-new-block'
					&& e.target.className !== 'add-new-block-input'
					&& e.target.className !== 'add-btn'
					&& this.isShowAddNewBlock){
				this.toggleAddNewWord();
			}
		};
	}

	getRandomString = () => {
		const okies = ['!', '!', '!', '!', '', '', '', ''];
		return okies[Math.floor(Math.random() * okies.length)];
	}

	render() {
		const classNames = require('classnames')
		const addNewClass = classNames({
      'add-new-container_text': true,
      'hide': !this.state.plusHover
		})

		return (
			<div style={'height: 85%'}>
				{ this.state.showStartPage ?
					<div className='startPage'>
						<div className='startPage_text'>     ,    ,        .</div>
						<div className='startPage_btn' onClick={this.hideStartPage}>{this.greet}</div>
					</div> : ''
				}
				<div className='container'>
					<img src='/assets/r.svg' alt='R' />
					<p className='line' />
					<div className='head'>
						<img src='/assets/flexicon.svg' alt='Flexicon' />
						<div className="add-new-container">
							<div className={addNewClass}>  </div>
							<img
								className='plus'
								src='/assets/plus.svg'
								alt='plus'
								onMouseEnter={this.showAddNew}
								onMouseLeave={this.hideAddNew}
								onClick={this.toggleAddNewWord}
							/>
							{
								this.state.showAddNewWord ?
									<div className='add-new-block'>
										<input
											type="text"
											placeholder=' '
											value={this.state.addWord}
											onInput={this.handleAddWordChange}
											className='add-new-block-input'
										/>
										<textarea
											placeholder=' '
											value={this.state.addTranslation}
											onInput={this.handleAddTranslationChange}
											className='add-new-block-input'
										/>
										<div className='add-btn' onClick={this.addTranslation}>+</div>

										{
											this.state.showWordAdded ? 
												<div className="wordAdded">
													<span className="wordAddedText">,     !       !</span>
													<div className="wordAddedOk" onClick={this.toggleAddNewWord}></div>
												</div> : ''
										}
									</div> : ''
							}
						</div>
					</div>
					{ 
						!this.state.showAddNewWord ?
							<div>
								<input
									type="text"
									className='text-to-translate'
									value={this.state.word}
									onInput={this.handleChange}
									placeholder=' '
								/>
								<div className='translation-result parent'>
									<span>{this.state.translation}</span>
								</div>
							</div> : ''		
					}
					<div className="shareContainer"
						style={this.state.showShare ? 'width: 100%' : ''}
						onMouseEnter={this.showShare}
						onMouseLeave={this.hideShare}
					>
						<div className="box">
							<img src='/assets/arrow.svg' />
							<span>share</span>
						</div>
						{
							this.state.showShare ?
								<div className="shareBlock">
									<a href='#' onClick={this.fbShare}>facebook</a>
									<a href={`https://telegram.me/share/url?url=${window.location.href}?word=${this.state.word}&text= `} target="_blank">telegram</a>
								</div> : ''
						}
					</div>
				</div>
			</div>
		);
	}
}



